import React from "react";
import { Col, Row, Table, Image, Card, Navbar } from "react-bootstrap";
import { Link } from "react-router-dom";
import withRouter from "../routes/WithRouter";
import { baseUrl, FontAwesomeIcon } from "../../Assets/AssetsHelper";
import { generateRandomArray } from "../../utils";
import "./auth.scss"
import { IS_TAGEMBED, LOGO } from "../../actions/api";
import { jsonData } from "./Authentication";
import { NavBar } from "./Init";
const Authentication = (props) => {
    const { children } = props;
    const onlyFormUrl = ['forgotpassword', 'resetpassword', 'accounts/otherinfo', 'collaborator/request', 'accounts/temp-pwd']
    const onlyForm = onlyFormUrl.some(path => props.location.pathname.includes(path))
    const isRedirect = ["/", "/accounts/login"].some(path => props.location.pathname.includes(path))
    return <div className={`min-h-100 ${!IS_TAGEMBED ? 'tb-theme' : ''}`}>
        {props.location.pathname.includes('accounts/otherinfo') && <NavBar />}
        <div className={`auth_bg bg-white${onlyForm ? ' auth_full_screen' : ''}${props.location.pathname.includes('accounts/otherinfo') ? ' other-info-main' : ''}`}>
            <Row className="gx-0 w-100 mx-0" style={{ minHeight: 'calc(100vh - 60px)' }}>
                <Col xxl={5} lg={6} className={`d-none d-lg-block bg-gradient-${IS_TAGEMBED ? '1' : '2'} ${onlyForm ? 'd-lg-none' : ''}`}>
                    <div className='d-flex flex-column flex-grow-1 min-h-100'>
                        <div className="px-xl-10 px-5">
                            <div className='d-flex flex-column hauto-smheight' style={{ height: '19vh' }}>
                                <div className="d-flex justify-content-center p-5">
                                    <Link to={isRedirect && !onlyForm ? IS_TAGEMBED ? 'https://tagembed.com/' : 'https://taggbox.com/' : '/'} className="d-block text-center" title={IS_TAGEMBED ? 'Tagembed' : 'Taggbox'}>
                                        <Image src={LOGO} width="170" height="34" alt={IS_TAGEMBED ? 'Tagembed' : 'Taggbox'} title={IS_TAGEMBED ? 'Tagembed' : 'Taggbox'} />
                                    </Link>
                                </div>
                                <p className="text-muted text-uppercase mb-3 fs-8 fw-medium text-center">{jsonData.platformTagline}</p>
                            </div>
                            <div className="hauto-smheight" style={{ height: '35vh', minHeight: 280 }}>
                                <Card className="rounded-3 mx-auto" style={{ boxShadow: '3px 6px 20px 0px #6B6B6B17', maxWidth: 520 }}>
                                    <Card.Body className="p-1">
                                        <div className="d-flex w-100">
                                            <div className="d-flex w-100 flex-shrink-0" style={{ maxWidth: 220 }}>
                                                <Image className="object-center-cover h-100 w-100 rounded-2" style={{ maxHeight: '250px', objectPosition: 'top' }} height={250} width={220} src={baseUrl(jsonData.userImage)} alt={jsonData.userImage} fluid />
                                            </div>
                                            <div className="d-flex flex-column p-4 position-relative">
                                                <div className="position-absolute start-0 top-0 px-3 pt-2">
                                                    <FontAwesomeIcon type={'solid'} icon={'quote-left'} iconClass={'display-1 fw-bold opacity-25'} />
                                                </div>
                                                <div className="position-relative my-3">
                                                    <p className="mb-5 mt-2">{jsonData.testimonial.desc}</p>
                                                    <p className="fw-bolder fs-8 mb-0">{jsonData.testimonial.author}</p>
                                                    <p className="fs-8 mb-0 text-muted">{jsonData.testimonial.position}</p>
                                                </div>
                                            </div>
                                        </div>
                                    </Card.Body>
                                </Card>
                            </div>
                        </div>
                        <div className="trusted_by w-100 hauto-smheight" style={{ height: '24vh' }}>
                            <Table bordered>
                                <tbody>
                                    <tr>
                                        {generateRandomArray(3).map((user, index) => (<td key={index} className="bg-transparent align-middle" style={{ borderRight: 0 }}>
                                            <div className='d-flex align-items-center justify-content-center'>
                                                <Image height={15} width={120} src={baseUrl(jsonData.companies.logs[index])} alt={jsonData.companies.logs[index]} fluid />
                                            </div>
                                        </td>))}
                                    </tr>
                                    <tr>
                                        {generateRandomArray(3).map((user, index) => (<td key={index} className="bg-transparent align-middle" style={{ borderRight: 0 }}>
                                            <div className='d-flex align-items-center justify-content-center'>
                                                <Image height={15} width={120} src={baseUrl(jsonData.companies.logs[index + 3])} alt={jsonData.companies.logs[index + 3]} fluid />
                                            </div>
                                        </td>))}
                                    </tr>
                                </tbody>
                            </Table>
                        </div>
                        <div className="f-center">
                            <Image height={128} width={100} src={baseUrl(jsonData.badgeImage)} alt={'G2 Badge'} fluid />
                            <div className="ms-xl-5 ms-3 flex-column d-flex">
                                <div className={`d-flex align-items-center justify-content-start mb-2`}  >
                                    {generateRandomArray(5).map((count, index) => (<div key={index} className='ps-1 fs-4' style={{ color: '#FF492C' }}>
                                        <FontAwesomeIcon type="solid" icon={'star'} />
                                    </div>))}
                                    <span className="fw-bolder fs-5 ms-3">4.9/5</span>
                                </div>
                                <p className="text-muted mb-0">{jsonData.reviewSummary}</p>
                            </div>
                        </div>
                    </div>
                </Col>
                <Col xxl={onlyForm ? 12 : 7} lg={onlyForm ? 12 : 6} className="d-flex align-items-center justify-content-center py-10 py-lg-0">
                    <div className="flex-column f-center flex-grow-1 p-5 p-lg-10 auth_form">
                        <div className="f-center flex-column flex-shrink-0 auth_form_area">
                            {children}
                        </div>
                    </div>
                </Col>
            </Row>
        </div>
    </div>
}
export default withRouter(Authentication)
